body {
  height: 100vh;
  background: radial-gradient(
    ellipse at bottom,
    #1b2735 0%,
    #090a0f 100%
  ); //渐变
  overflow: hidden;
  filter: drop-shadow(0 0 10px white);
}

//random
@function random_range($min, $max) {
  $rand: random();
  $random_range: $min + floor($rand * (($max - $min)+1));
  @return $random_range;
}

.money {
  $total: 200;
  position: absolute;
  width: 30px;
  height: 30px;
  // background: white;
  background-image: url(./img/1.svg);
  background-size: cover;
  // border-radius: 50%;

  @for $i from 1 through $total {
    $random-x: random(1000000) * 0.0001vw;
    $random-offset: random_range(-100000, 100000) * 0.001vw;
    $random_x-firstEnd: $random-x + $random-offset;
    $random-x-secondEnd: $random-x + calc($random-offset/2);
    $random-down-y: calc(random_range(30000, 80000) / 100000);
    $random-down-height: $random-down-y * 100vh;
    $random-scale: random(1000) * 0.001;
    $fall-duration: random_range(5, 15) * 1s;
    $fall-delay: random(30) * -1s;
    $rotate: random(360) * 1deg;

    $random-img:ceil(random(3));

    &:nth-child(#{$i}) {
      background-image:url(./img/#{$random-img}.svg);
      opacity: random(10000) * 0.0001;
      transform: translate($random-x, -10px) scale($random-scale);
      animation: fall-#{$i} $fall-duration $fall-delay linear infinite;
    }

    @keyframes fall-#{$i} {
      #{percentage($random-down-y)} {
        transform: translate($random-x, $random-down-height)
          scale($random-scale) rotate($rotate);
      }

      to {
        transform: translate($random-x, 100vh) scale($random-scale) rotate($rotate);
      }
    }
  }
}
